.loginPage {
  min-height: 100%;
  padding: 15px;
  background: linear-gradient(grey, white, blue);
}

.header {
  color: blue;
}

.errorForm {
  width: fit-content;
  padding: 10px;
  margin-top: 15px;
  border: 2px solid rgba(255, 0, 0, 0.8);
  font-weight: bold;
  font-size: 14px;
  color: rgba(255, 0, 0, 0.8);
}

.inputBox {
  position: relative;
  width: max-content;
  margin-bottom: 20px;
}

.input {
  width: 300px;
  padding: 6px;
  border: 1px solid rgba(0, 0, 255, 0.8);
  border-radius: 5px;
  font-size: 14px;
  color: rgba(0, 0, 255, 0.8);
}

.input.error {
  border: 1px solid rgba(255, 0, 0, 0.8);
  color: rgba(255, 0, 0, 0.8);
}

.input::placeholder {
  color: rgba(0, 0, 255, 0.3);
}

.input:focus-visible {
  outline: unset;
  border: 2px solid rgba(0, 0, 255, 0.8);
}

.checkboxContainer {
  display: flex;
  align-items: center;
}

.checkbox {
  margin-right: 10px;
  cursor: pointer;
}

.label {
  font-size: 14px;
  color: rgba(0, 0, 255, 0.8);
}

.btnSubmit {
  padding: 6px 15px;
  border: 1px solid black;
  border-radius: 5px;
  background: rgba(0, 0, 255, 0.8);
  font-size: 14px;
  color: white;
}

.btnSubmit:hover, .btnSubmit:active {
  border: 2px solid rgba(0, 0, 255, 0.8);
  background: white;
  color: rgba(0, 0, 255, 0.8);
  cursor: pointer;
}

.btnSubmit:active {
  background: rgba(0, 0, 255, 0.3);
}

.btnSubmit:disabled {
  border: 1px solid rgb(163, 163, 163);
  background: white;
  color: rgb(163, 163, 163);
  cursor: unset;
}

.errorMessage {
  position: absolute;
  top: 30px;
  font-size: 12px;
  color: red;
  z-index: 5;
}

.passBtn {
  position: absolute;
  top: 5px;
  right: 5px;
  width: 20px;
  height: 20px;
  border: none;
  background: transparent;
}

.passBtn img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
}

.captchaBox {
  margin-top: 15px;
}

.captchaImg {
  margin-top: 20px;
}
